// Utility class used to interact
//import store from "./store"

// with the database realted to guesses
const url = '/api/guess'

class DataService {
  // Get the guesses
  static getData() {
    return new Promise((resolve, reject) => {
      fetch(url)
      .then(resp => resp.json())
      .then(data => {
        resolve(data.map(guess => {
          return {
            ...guess
          }
        }))
      })
      .catch(error => {
        reject(error)
      })
    })
  }
  // Post a guess
  static postGuess(data) {
    return fetch(url + '/new', {
      method: 'POST',
      cache: 'no-cache',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
        //'x-csrf-token': store.state.csrfToken
      },
      body: JSON.stringify(data)
    })
    .then(resp => resp.json())
    .catch(error => new Error(error))
  }
  // Delete a guess
  static deleteGuess(id) {
    return fetch(`${url}/${id}`, {
      method: 'DELETE',
      cache: 'no-cache'
    })
    .then(resp => resp.json())
    .catch(error => new Error(error))
  }
}
export default DataService
