// Class to interact with Users collection
class AuthenticationService {
  static getUser() {
    return new Promise((resolve, reject) => {
       fetch('/api/user', {
        method: 'GET',
        cache: 'no-cache',
        credentials: 'include'
      })
      .then(resp => {
        if(resp.ok) {
          return resp.json()
        }
      })
      .then(user => resolve({
        ...user
      }))
      .catch(error => reject(error))
    })
  }
}

export default AuthenticationService
