<template>
<div>
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">Guessed By</th>
          <th class="text-left">Guessed On</th>
          <th class="text-left">Baby's Birthday</th>
          <th class="text-left">Baby's Arrival Time</th>
          <th class="text-left">Baby's Weight (g)</th>
          <th class="text-left"
            v-if="$store.state.isLoggedIn"
          >
            Delete
          </th>

        </tr>
      </thead>
      <tbody>
        <tr v-for="guess in guesses" :key="guess.id">
          <td>{{ guess.author }}</td>
          <td>{{ shortDate(guess.createdAt) }}</td>
          <td>{{ longDate(`${guess.birthday}T05:00:00`) }}</td>
          <td>{{ guess.time }}</td>
          <td>{{ guess.weight }}</td>
          <td>
            <v-icon 
              @click="confirmRemove(guess._id)"
              v-if="guess.author == userExists"
            >
              mdi-delete
            </v-icon>
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
  <div class="text-center">
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <v-card>
        <v-card-title
          class="headline lighten-2"
          primary-title
        >
          Delete this guess?
        </v-card-title>

        <v-card-text>
          This action cannot be reversed.
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="error"
            @click="remove()"
            :absolute="true"
            left
          >
            Yes, delete
          </v-btn>
          <v-btn
            color="primary"
            text
            @click="cancelRemove()"
          >
            Cancel
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</div>
</template>
<script>
import DataService from '../DataService'
export default {
  name: 'Submissions',
  data() {
    return {
      guesses: [],
      error: null,
      dialog: false,
      pendingRemove: null,
    }
  },
  computed: {
    userExists() {
      return this.$store.state.currentUser == null ? '' : this.$store.state.currentUser.name
    },
  },
  async created() {
    try {
      this.guesses = await DataService.getData()
    } catch(error) {
      this.error = error
    }
  },
  methods: {
    confirmRemove(id) {
      this.pendingRemove = id
      this.dialog = true
    },
    remove() {
      this.dialog = false
      const id = this.pendingRemove
      DataService.deleteGuess(id).then(() => { 
        this.guesses = this.guesses.filter(item => {
          return item._id != id
        })
        this.pendingRemove = null
      })
    },
    cancelRemove() {
      this.pendingRemove = null
      this.dialog = false
    },
    shortDate(date) {
      return new Intl.DateTimeFormat(
        'en-US',
        {
          month: 'short',
          day: 'numeric',
          hour: 'numeric',
          minute: 'numeric'
        }
      ).format(new Date(date))
    },
    longDate(date) {
      return new Intl.DateTimeFormat(
        'en-US',
        {
          month: 'long',
          day: 'numeric'
        }
      ).format(new Date(date))
    },
  },
}
</script>
