<template>
<div>
  <v-card
    :loading="loading"
  >
    <v-form class="pa-2">
      <v-menu
        v-model="dateMenu"
        :close-on-content-click="false"
        transition="scale-transition"
        offset-y
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="birthday"
            label="Baby's birthday?"
            prepend-icon="mdi-cake"
            readonly
            v-on="on"
            :error-messages="birtdayErrors"
            @input="$v.birthday.$touch()"
            @blur="$v.birthday.$touch()"
          >
          </v-text-field>
        </template>
        <v-date-picker v-model="birthday" @input="dateMenu = false"></v-date-picker>
      </v-menu>
      <v-menu
        v-model="timeMenu"
        :close-on-content-click="false"
        transition="scale-transition"
        offset-y
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="time"
            label="Baby's birth time?"
            prepend-icon="mdi-clock"
            readonly
            v-on="on"
            :error-messages="timeErrors"
            @input="$v.time.$touch()"
            @blur="$v.time.$touch()"
          >
          </v-text-field>
        </template>
        <v-time-picker v-model="time" @input="timeMenu = false"></v-time-picker>
      </v-menu>
      <v-text-field
        v-model="weight"
        label="Baby's birth weight?"
        prepend-icon="mdi-weight"
        suffix="grams"
        :error-messages="weightErrors"
        @input="$v.weight.$touch()"
        @blur="$v.weight.$touch()"
      >
      </v-text-field>
    </v-form>
    <v-card-actions>
      <v-btn
        color="success"
        text
        @click="submit()"
      >
        Submit
      </v-btn>
      <v-btn
        color="error"
        text
        :absolute="true"
        right
        @click="clear()"
      >
        Reset
      </v-btn>
    </v-card-actions>
  </v-card>
  <v-snackbar
    v-model="snackbarSuccess"
    top
    color="success"
    :timeout=6000
  >
    <v-icon color="#fff">mdi-checkbox-marked-circle</v-icon>
    Guess saved!
    <v-btn 
      text
      :to="{ name: 'Guess' }"
    >
      View guesses
    </v-btn>
  </v-snackbar>
  <v-snackbar
    v-model="snackbarError"
    top
    color="error"
    :timeout=6000
  >
    <v-icon color="#fff">mdi-emoticon-sad-outline</v-icon>
    Oh snap! Someting went wrong!
  </v-snackbar>
</div>
</template>
<script>
import { validationMixin } from 'vuelidate'
import { required, between } from 'vuelidate/lib/validators'
import DataService from '../DataService'

export default {
  mixins: [ validationMixin ],
  name: 'GuessForm',
  data() {
    return {
      dateMenu: false,
      timeMenu: false,
      birthday: '',
      time: '',
      weight: '',
      loading: false,
      snackbarSuccess: false,
      snackbarError: false,
    }
  },
  computed: {
    birtdayErrors() {
      const errors = []
      if (!this.$v.birthday.$dirty) return errors
      !this.$v.birthday.required && errors.push('Birthday is required.')
      return errors
    },
    timeErrors() {
      const errors = []
      if (!this.$v.time.$dirty) return errors
      !this.$v.time.required && errors.push('Time is required.')
      return errors
    },
    weightErrors() {
      const errors = []
      if (!this.$v.weight.$dirty) return errors
      !this.$v.weight.between && errors.push('Weight can only range from 2000 to 8000 grams')
      !this.$v.weight.required && errors.push('Weight is required.')
      return errors
    },
  },
  validations: {
    birthday: {
      required
    },
    time: {
      required
    },
    weight: {
      required,
      between: between(2000, 8000)
    }
  },
  methods: {
    submit() {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        this.loading = true
        DataService.postGuess({
          author: this.$store.state.currentUser.name,
          birthday: this.birthday,
          time: this.time,
          weight: this.weight,
          _csrf: this.$store.state.csrfToken
        })
        .then(resp => {
          if(!resp.ok) {
            setTimeout(() => {
              this.loading = false
              this.snackbarError = true
              this.clear()
            }, 2000)
          } else {
            setTimeout(() => {
              this.loading = false
              this.snackbarSuccess = true
              this.clear()
            }, 2000)
          }
        })
        .catch(error => {
          console.log(error)
          setTimeout(() => {
            this.loading = false
            this.snackbarError = true
            this.clear()
          }, 2000)
        })
      }
    },
    clear() {
      this.$v.$reset()
      this.birthday = ''
      this.time = ''
      this.weight = ''
    },
  },
}
</script>
